import jwt from 'jsonwebtoken';

export const makePostRequest = (data, route) => {
  const url = process.env.NODE_ENV === 'production'
    ? `https://pwa-workshop.herokuapp.com/${route}`
    : `/${route}`

  return fetch(url, {
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    },
    method: 'post',
    body: JSON.stringify(data)
  }).then(response => response.json())
    .then(data => {
      if (data.success) {
        const secret = 'ilovepwa'
        return jwt.verify(data.token, secret, (err, decoded) => {
          if (err) {
            return {authorized: false, err}
          } else {
            return {user: decoded.user, token: data.token}
          }
        })
      }
    })
}

export const createOnePost = ({creator, title, content, postData}, token) => {
  const url = process.env.NODE_ENV === 'production'
    ? `https://pwa-workshop.herokuapp.com/create/post`
    : `/create/post`

  return fetch(url, {
    headers: {
      'Content-Type': 'application/json',
      'x-access-token': token,
      'Accept': 'application/json'
    },
    method: 'post',
    body: JSON.stringify({post: {
      creator,
      title,
      content,
      postData
    }})
  }).then(response => response.json())
    .then(data => {
      return data
    })
}

export const fetchAllPosts = token => {
  const url = process.env.NODE_ENV === 'production'
    ? `https://pwa-workshop.herokuapp.com/posts`
    : `/posts`

  return fetch(url, {
    headers: {
      'Content-Type': 'application/json',
      'x-access-token': token,
      'Accept': 'application/json'
    },
    method: 'get'
  }).then(response => response.json())
    .then(data => {
      return data
    })
}

export const deleteOnePost = (token, id) => {
  const url = process.env.NODE_ENV === 'production'
    ? `https://pwa-workshop.herokuapp.com/post/${id}`
    : `/post/${id}`

  return fetch(url, {
    headers: {
      'Content-Type': 'application/json',
      'x-access-token': token,
      'Accept': 'application/json'
    },
    method: 'delete'
  }).then(response => response.json())
    .then(data => {
      console.log(data)
      return data
    })
}


